export const modifyimsi = (text) => ({
    type: 'MODIFY_IMSI',
    text
})
export const modifymsisdn = (text) => ({
    type: 'MODIFY_MSISDN',
    text
})
export const modifyelement = (text) => ({
    type: 'MODIFY_MME',
    text
})


